import * as Joi from 'joi';

import { RegExpEnum } from '../../constants';

export const userValidator = Joi.object({
    name_user: Joi.string().trim().min(2).max(25).required(),
    phone: Joi.string().trim().regex(RegExpEnum.phone).required(),
    role_id: Joi.number().integer().min(1).required(),
    type_user: Joi.number().integer().min(1).allow('', null)
});
